            rel="noopener noreferrer"
            >https://dungeon.wombat.app/nfts</a
          >
          (06/08/2022)
        </p>
        <p>Price in USD: ${{ usdprice }}</p>
      </div>
      <div class="col-md"></div>
    </div>
    <div class="row">
      <!-- <div class="col-md">
        <label class="form-label">Season MP:</label>
        <select v-model="season_price" class="form-select">
          <option value="current" selected>Current Season MP</option>
