        <select class="form-control" v-model="filters.mp.value">
          <option
            v-for="option in mpOptions"
            v-bind:key="option"
            v-bind:value="option"
          >
            {{ option }}
          </option>
        </select>
      </div>
      <div class="col-md">
        <label class="typo__label">Materials: </label>
        <!-- v-model="selected_material" -->
        <multiselect
          v-model="filters.material_name.value"
          :options="material_list"
          :multiple="true"
          :close-on-select="false"
          :clear-on-select="false"
          :preserve-search="true"
          placeholder="All selected"
          label="name"
          track-by="name"
          :preselect-first="false"
        >
          <!-- <template slot="singleLabel" slot-scope="{ values, isOpen }"><img class="option__image" :src="require('@/assets/img/' + value.name + '.svg')" alt="No Man’s Sky"><span class="option__desc"><span class="option__title">asd</span></span></template> -->
          <template slot="selection" slot-scope="{ values, isOpen }">
            <span
              class="multiselect__single"
              v-if="values.length &amp;&amp; !isOpen"
              >{{ values.length }} options selected</span
            ></template
          >
          <template slot="option" slot-scope="props">
            <img
              class="option__image"
              height="20px"
              :src="require('@/assets/img/' + props.option.name + '.svg')"
            />
            {{ props.option.name }}
          </template>
        </multiselect>
      </div>
      <div class="col-md"></div>
    </div>
    <div class="row">
      <div class="col-md">
        <label class="form-label">Pages (100 assets per page):</label>
        <input type="number" v-model="page" class="form-control" />
      </div>
      <div class="col-md">
        <label class="typo__label">Collections: </label>
        <multiselect
          v-model="selected_collection"
          :options="collections"
          :multiple="true"
          :close-on-select="false"
          :clear-on-select="false"
          :preserve-search="true"
          placeholder="All selected"
          label="name"
          track-by="name"
          :preselect-first="false"
        >
          <template slot="selection" slot-scope="{ values, isOpen }"
            ><span
              class="multiselect__single"
              v-if="values.length &amp;&amp; !isOpen"
              >{{ values.length }} options selected</span
            ></template
          >
          <template slot="option" slot-scope="props">
            <img
              class="option__image"
              height="20px"
              :src="require('@/assets/img/' + props.option.material + '.svg')"
            />
            &nbsp;{{ props.option.name }}
          </template>
        </multiselect>
      </div>
      <div class="col-md">
        <label class="typo__label"></label><br />
        <button class="btn btn-success" v-on:click="searchlist()">
          <b-icon icon="search"></b-icon>
        </button>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-md">
          <p></p>
          <div class="form-check form-switch">
            <input
              v-model="tblfield"
              class="form-check-input"
              type="checkbox"
              id="flexSwitchCheckDefault"
            />
            <label class="form-check-label" for="flexSwitchCheckDefault"
              >Table Fields ({{ tblfield ? "Full" : "Simple" }})</label
            >
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md">
          <p v-show="isLoading" class="text-center">
            Loading Page: {{ current_page }}/{{ page }}
          </p>
          <div v-show="!SearchEmpty">
            <hr />
            <v-table
              :data="list"
              :hideSortIcons="true"
              class="table table-bordered"
              :filters="filters"
            >
              <thead slot="head">
                <th v-show="tblfield">Supply</th>
                <th v-show="tblfield">Template ID</th>
                <th v-show="tblfield">Mint</th>
                <th v-show="tblfield">Collection</th>
                <v-th sortKey="name"><i class="fa fa-sort"></i>Name</v-th>
                <v-th v-show="tblfield" sortKey="usd_price"
                  ><i class="fa fa-sort"></i>USD Price</v-th
                >
                <v-th sortKey="price"><i class="fa fa-sort"></i>Price</v-th>
                <v-th sortKey="mp"><i class="fa fa-sort"></i>Mining Power</v-th>
                <v-th sortKey="usd_mp"><i class="fa fa-sort"></i>USD/MP</v-th>
                <th>Material</th>
                <th style ="text-align:center">Link</th>
              </thead>
              <tbody slot="body" slot-scope="{ displayData }">
                <tr v-for="row in displayData" :key="row.id">
                  <td v-show="tblfield">{{ row.supply }}</td>
                  <td v-show="tblfield">{{ row.template_id }}</td>
                  <td v-show="tblfield">{{ row.mint }}</td>
                  <td v-show="tblfield">{{ row.collection }}</td>
                  <td>{{ row.name }}</td>
                  <td v-show="tblfield">{{ row.usd_price }}</td>
                  <td>{{ row.price }}</td>
                  <td>{{ row.mp }}</td>
                  <td>{{ row.usd_mp }}</td>
                  <td>
                    <center>
                      <img
                        :data-mat="row.material_name"
                        height="20px"
                        :src="row.material_url"
                      />
                    </center>
                  </td>
                  <td>
                    <div class = "btn-max-height">
                      <a
                        :href="row.link_string"
                        target="_blank"
                        class="btn btn-sm btn-dark"
                        ><img
                          height="15px"
                          src="https://wax.atomichub.io/images/logos/atomic.svg"
                      /></a>&nbsp;
                      <a
                        v-show="row.symbol=='WAX'"
                        :href="row.link_string_nefty"
                        target="_blank"
                        class="btn btn-sm btn-dark"
                        ><img
                          height="15px"
                          src="https://dashboard-assets.dappradar.com/document/6874/neftyblocks-dapp-marketplaces-wax-logo-166x166_b6f6ed8ddbad62ff599f75198853792e.png"
                      /></a>
                    </div>
                  </td>
                </tr>
              </tbody>
            </v-table>

            <!-- :css="css.table" -->
            <!-- <vuetable
              ref="vuetable"
              :fields="fields"
              :api-mode="false"
              :data="list"
              :sort-order="sortOrder"
            ></vuetable> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Vuetable from "vuetable-2";
import listData from "../assets/list.json";
import lastupdate from "../assets/date.json";
// import CssForBootstrap4 from "../assets/js/tablecss";
import Multiselect from "vue-multiselect";

export default {
  name: "ListTable",
  components: {
    // Vuetable,
    Multiselect,
  },
  props: {},
  data() {
    return {
      filters: {
        material_name: { value: "", custom: this.material_filter },
        mp: { value: "", custom: this.mp_filter },
      },
      searchhist: null,
      mpOptions: null,
      tblfield: true,
      SearchEmpty: true,
      minMP: null,
      range: {
        min: 5,
        max: 10000,
      },
      season_price: "current",
      material_list: [{ name: "Uranium" }, { name: "Iron" }, { name: "Coal" }],
      selected_material: [],
      lastupdate,
      isLoading: false,
      fullPage: true,
      // css: CssForBootstrap4,
      copy_new_mp_list: null,
      copy_mp_list: listData,
      listData: listData,
      symbol: "EOS",
      fields: null,
      field_opt: {
        full: [
          {
            name: "supply",
            title: "Supply",
          },
          {
            name: "template_id",
            title: "Template ID",
          },
          {
            name: "mint",
            title: "Mint",
          },
          {
            name: "collection",
            title: "Collection",
          },
          {
            name: "name",
            title: "Name",
          },
          {
            name: "usd_price",
            title: "USD price",
          },
          {
            name: "price",
            title: "Price",
            sortField: "price",
          },
          {
            name: "mp",
            title: "Mining Power",
          },
          {
            name: "usd_mp",
            title: "USD/MP",
          },
          {
            name: "material",
            title: "Material",
          },
          {
            name: "link",
            title: "Link",
          },
        ],
        simple: [
          {
            name: "name",
            title: "Name",
          },
          {
            name: "price",
            title: "Price",
            sortField: "price",
          },
          {
            name: "mp",
            title: "Mining Power",
          },
          {
            name: "usd_mp",
            title: "USD/MP",
          },
          {
            name: "material",
            title: "Material",
          },
          {
            name: "link",
            title: "Link",
          },
        ],
      },
      sortOrder: [
        {
          field: "price",
          direction: "asc",
        },
      ],
      list: [],
      collections: [],
      selected_collection: [],
      collection: "womplayextra",
      page: 5,
      current_page: 0,
      num_items: 100,
      items: [],
      usdprice: 0.0,
      sortby: "usd_mp",
      items_copy: [],
      oloader: null
    };
  },
  created() {
    this.priceUsd();
    this.getCollections();
    this.getMPRange();
    this.fields = this.field_opt.full;
    this.mpOptions = this.getMPList().sort((a, b) => a - b);
  },
  watch: {
    symbol: function () {
      this.priceUsd();
      this.getCollections();
    },
    sortby: function (x) {
      this.updateSort(x);
    },
    // selected_material: function () {
    //   this.filterTable("material");
    // },
    // minMP: function (x) {
    //   if (x == null || x == "") {
    //     this.minMP = this.range.min;
    //   }

    //   this.filterTable("material");
    // },
    season_price: function () {
      this.changeSeasonPrice();
      if (this.searchhist != null) {
        this.drawTable();
      }
    },
    tblfield: function (x) {
      console.log("switch", x);
      if (x) {
        this.fields = this.field_opt.full;
      } else {
        this.fields = this.field_opt.simple;
      }
      console.log("fields", this.fields);
    },
  },
  methods: {
    imgurl: function (mat) {
      return require("@/assets/img/" + mat + ".svg");
    },
    changeSeasonPrice: function () {
      // if (this.season_price == "current") {
      //   this.listData = this.copy_mp_list;
      // } else {
      //   if (this.copy_new_mp_list == null) {
      //     console.log("Not Empty");
      //     this.copy_new_mp_list = this.copy_mp_list.map(function (x) {
      //       return {
      //         collection: x.collection,
      //         material: x.material,
      //         mining_power: x.new_mining_power,
      //         new_mining_power: x.new_mining_power,
      //         new_material: x.new_material,
      //         symbol: x.symbol,
      //         template: x.template,
      //       };
      //     });
      //   }
      //   this.listData = this.copy_new_mp_list;
      // }

      // this.getMPRange();
      this.getCollections();
    },
    getMPRange: function () {
      var getmin = Math.min(...this.listData.map((o) => o.mining_power));
      this.range.min = getmin == 0 ? 5 : getmin;
      this.range.max = Math.max(...this.listData.map((o) => o.mining_power));
      this.filters.mp.value = 5;
    },
    updateSort: function () {
      var varsort = this.sortby;
      this.items.sort((a, b) => {
        if (varsort == "price") {
          return a.price - b.price;
        }
        if (varsort == "mp") {
          return a.mp - b.mp;
        }
        if (varsort == "usd_mp") {
          return a.usd_mp - b.usd_mp;
        }
      });

      this.list = this.items;
      // this.filterTable("material");
    },
    material_filter: function (filterValue, row) {
      var val = [];
      if (filterValue.length > 0) {
        filterValue.forEach(function (item) {
          val.push(item.name);
        });
      }

      if (val.includes(row.material_name) || val.length == 0) {
        return true;
      }
      return false;
    },
    mp_filter: function (filterValue, row) {
      if (parseInt(row.mp) >= parseInt(filterValue)) {
        return true;
      }
      return false;
    },
    // filterTable: function (filter) {
    //   var newData = this.items_copy;
    //   var tlist = [];
    //   var tMP = this.minMP;

    //   if (filter == "materialxxxxx") {
    //     if (this.selected_material.length > 0) {
    //       var val = [];
    //       this.selected_material.forEach(function (item) {
    //         val.push(item.name);
    //       });

    //       newData.forEach(function (item) {
    //         if (val.includes(item.material_name) && item.mp >= tMP) {
    //           tlist.push(item);
    //         }
    //       });

    //       this.list = tlist;
    //     } else {
    //       newData.forEach(function (item) {
    //         if (item.mp >= tMP) {
    //           tlist.push(item);
    //         }
    //       });
    //       this.list = tlist;
    //     }
    //   }
    // },
    getCollections: function () {
      this.collections = [];
      var tcol = this.collections;
      var tsym = this.symbol;
      var sp = this.season_price;
      this.listData.forEach((x) => {
        if (x.symbol == tsym) {
          if (!tcol.some((el) => el.name === x.collection)) {
            if (sp == "current"){
              tcol.push({ name: x.collection, material: x.material });
            }
            else{
              tcol.push({ name: x.collection, material: x.new_material });
            }
          }
        }
      });

      this.collections.sort();
    },
    getMPList: function () {
      return this.listData
        .map((item) => item.mining_power)
        .filter(
          (value, index, self) => parseInt(self.indexOf(value)) === index
        );
    },
    concatCollection: function () {
      var temp_collection = [];
      this.collection = null;

      if (this.selected_collection.length > 0) {
        this.selected_collection.forEach((x) => {
          temp_collection.push(x.name);
        });
        this.collection = temp_collection.join(",");
      } else {
        this.collections.forEach((x) => {
          temp_collection.push(x.name);
        });
        this.collection = temp_collection.join(",");
      }
    },
    overlayloader: function (){
      this.isLoading = true;
      this.oloader = this.$loading.show({
        container: null,
        canCancel: false,
      });
    },
    searchlist: async function () {
      this.list = []
      var url;
      this.concatCollection();
      var TotalData = [];
      var datalen = 100;
      this.current_page = 0;

      this.overlayloader();

      while (datalen == 100 && this.current_page != this.page) {
        this.current_page++;
        url =
          "https://" +
          this.symbol +
          ".api.atomicassets.io/atomicmarket/v1/sales?collection_name=" +
          this.collection +
          "&page=" +
          this.current_page +
          "&limit=" +
          this.num_items +
          "&state=1&order=desc&sort=created";
        const response = await fetch(url);
        var data = await response.json();

        TotalData = TotalData.concat(data["data"]);
        datalen = data["data"].length;
      }

      this.searchhist = TotalData;
      this.drawTable();
      this.SearchEmpty = false;
    },

    drawTable: async function () {
      await this.priceUsd();
      var searchdata = this.searchhist;
      var ts = this;
      var t = (this.items = []);
      var dv = this.symbol == "EOS" ? 10000 : 100000000;

      searchdata.forEach((x) => {
        var name =
          "name" in x["assets"][0]
            ? x["assets"][0]["name"]
            : x["assets"][0]["collection"]["name"];
        var price_symbol = parseInt(x["price"]["amount"]) / dv;
        var usd_value = price_symbol * this.usdprice;
        var getListData = this.getListData(x);
        var mp = getListData["mining_power"];
        var material = getListData["material"];
        var usdmp = (usd_value / mp).toFixed(4);

        if (mp > 0) {
          t.push({
            symbol: this.symbol,
            id: parseInt(x["sale_id"]),
            collection: x["assets"][0]["collection"]["name"],
            template_id: x["assets"][0]["template"]["template_id"],
            mint: x["assets"][0]["template_mint"],
            supply:
              x["assets"][0]["template"]["issued_supply"] +
              "/" +
              x["assets"][0]["template"]["max_supply"],
            name: name,
            link_string:
              "https://" +
              ts.symbol +
              ".atomichub.io/market/sale/" +
              x["sale_id"],
            link_string_nefty:
              "https://neftyblocks.com/marketplace/listing/" + x["sale_id"],
            link:
              "<a href='https://" +
              ts.symbol +
              ".atomichub.io/market/sale/" +
              x["sale_id"] +
              "' target='_blank' class = 'btn btn-dark'><img height='20px' src='https://wax.atomichub.io/images/logos/atomic.svg'></a>",
            price: price_symbol,
            usd_price: "$" + usd_value.toFixed(2),
            mp: mp,
            usd_mp: usdmp,
            material_url: require("@/assets/img/" + material + ".svg"),
            material:
              material != null
                ? "<img height='30px' src='" +
                  require("@/assets/img/" + material + ".svg") +
                  "'>"
                : "",
            material_name: material,
          });
        }
      });
      this.list = [];

      this.updateSort();
      this.items_copy = this.items;
      this.list = this.items;

      // this.filterTable("material");
      this.fields[6]["title"] = "Price (" + this.symbol + ")";

      this.isLoading = false;
      this.oloader.hide();
    },
    priceUsd: async function () {
      var url =
        "https://" +
        this.symbol +
        ".api.atomicassets.io/atomicmarket/v1/config";
      const response = await fetch(url);
      const data = await response.json();
      this.usdprice =
        parseInt(data["data"]["supported_pairs"][0]["data"]["median"]) / 10000;
    },
    getListData: function (x) {
      if (x["assets"][0]["template"] != null) {
        var temId = parseInt(x["assets"][0]["template"]["template_id"]);
        var coll = x["assets"][0]["collection"]["collection_name"];
        var index = this.listData.findIndex(
          (x) => x.template === temId && x.collection === coll
        );
        if (index != -1) {
          var season_mp =
            this.season_price == "current"
              ? "mining_power"
              : "new_mining_power";
          var season_mats =
            this.season_price == "current" ? "material" : "new_material";
          return {
            mining_power: parseInt(this.listData[index][season_mp]),
            material: this.listData[index][season_mats],
          };
        }
      }
      return { mining_power: 0, material: null };
    },
  },
};
</script>

